/* SummaryCard.css */

.summary-card-container {
  display: flex;
  justify-content: center; /* Center the cards within the container */
  flex-wrap: wrap; /* Allows wrapping if the screen is too narrow */
  gap: 20px; /* Add some space between the cards */
}

.summary-card {
  perspective: 1000px;
  width: 400px;
  height: 300px;
  cursor: pointer;
  margin: 20px; /* Add margin for spacing between cards */
  position: relative; /* Ensure the card itself is positioned correctly */
}

.summary-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.summary-card.flipped .summary-card-inner {
  transform: rotateY(180deg);
}

.summary-card-front, .summary-card-back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  background-color: #fff;
  box-sizing: border-box;
}

.summary-card-back {
  transform: rotateY(180deg);
}

.summary-icon-large {
  width: 300px;
  height: 200px;
  border-radius: 10px;
}

.summary-title {
  margin: 10px 0;
  font-size: 24px;
  color: #2c3e50;
}

.summary-description {
  font-size: 16px;
  color: #7f8c8d;
  text-align: center;
  margin-top: 10px;
}
