.menu-button {
    background-color: transparent;
    border: none;
    color: #2c3e50;
    padding: 10px 15px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s; /* Add background-color to transition */
    border-radius: 20px;
  }
  
  .menu-button:hover {
    color: #f1f1f1;
    background-color: #2c3e50; /* Darker shade for hover */
  }
  