.contact-section {
  padding: 40px 20px;
  text-align: center;
  background-color: #f1f1f1;
  margin-top: 40px; /* Space above the section */
}
  
.contact-section h2 {
  margin-bottom: 30px;
  font-size: 24px;
}
  
.contact-section p {
  font-size: 16px;
  margin: 10px 0;
}
  
.contact-section a {
  color: #007bff;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
  
.contact-section a:hover {
  text-decoration: underline;
}
  
.icon {
  margin-right: 8px;
  font-size: 20px;
}
