html {
  scroll-behavior: smooth;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  background-color: #f1f1f1;
}

.header-title {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 24px;
  font-weight: bold;
  color: #2c3e50;
}

nav {
  display: flex;
}

.hero {
  position: relative;
  width: 100%;
  text-align: left;
}

.hero-image {
  width: 100%;
  height: auto;
}

.hero-text {
  position: absolute;
  top: 14%;
  left: 10%;
  transform: translate(-20%, -20%);
  color: white;
}

.hero-subtitle {
  font-size: 16px;
  font-weight: normal;
}

.hero-title {
  font-size: 48px;
  font-weight: bold;
  margin: 0; /* Remove default margin from h1 */
}

@media (max-width: 768px) {
  .hero-subtitle {
    font-size: 14px; /* Adjust font size for smaller screens */
  }

  .hero-title {
    font-size: 36px; /* Adjust font size for smaller screens */
  }
}
