.carousel-container {
  width: 80%;
  margin: 0 auto;
  padding: 20px 0;
}
  
.carousel-slide {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
  
.carousel-logo {
  max-width: 100px;
  max-height: 50px;
}

.logo-carousel-section {
  text-align: center;
  padding: 20px 0; /* Add some padding for spacing */
}
  
.logo-carousel-section h2 {
  margin-bottom: 20px; /* Add some space below the heading */
  font-size: 24px;
  color: #2c3e50;
}
