/* Resume.css */

.resume-section {
  padding: 0px 20px;
  text-align: center;
  background-color: #ffffff;
  margin-top: 40px;
}
  
.resume-section h2 {
  margin-bottom: 30px;
  font-size: 24px;
}
  
.resume-download-button {
  display: inline-block;
  padding: 10px 20px;
  background: #007bff;
  color: white;
  border-radius: 5px;
  text-decoration: none;
  font-size: 16px;
}
  
.resume-download-button:hover {
  background: #0056b3;
}
  
.icon {
  margin-right: 8px;
  font-size: 20px;
}
